import { Component, OnInit, Renderer2, ViewChild, ElementRef } from '@angular/core';
import { Contact } from '../shared/model/contact';
import { HttpClient, HttpHeaders, HttpParams, HttpErrorResponse } from '@angular/common/http';
import { TimeoutError, throwError } from 'rxjs';
import { catchError, timeout, map, mapTo } from 'rxjs/operators';
import { Renderer3 } from '@angular/core/src/render3/interfaces/renderer';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  formspreeUrl: string = 'https://stancu.net/dan.stancu@gmail.com';

  model: Contact = {
    name: "ds",
    email: "dan.stancu@gmail.com",
    message: "test message" 
  };

  formspree: any = '<h1>xxx</h1>';

  submitted: boolean = false;

  @ViewChild("dynamicDiv", {read: ElementRef}) private dynamicDiv: ElementRef;

  constructor(private httpClient: HttpClient) { }

  onSubmit() {
    const headers = new HttpHeaders();
    headers.set('Content-Type', 'application/x-www-form-urlencoded');

    const params = new HttpParams();
    params.set('name', this.model.name)
    .set('email', this.model.email)
    .set('message', this.model.message);

    const formData = new FormData();
    formData.append('name', this.model.name);
    formData.append('email', this.model.email);
    formData.append('message', this.model.message);

    this.httpClient.post(this.formspreeUrl, formData, {
      headers: {
        'Accept': '*/*',
        'Access-Control-Allow-Origin': '*'
      }}
    )
    .pipe(
      timeout(5000),
      catchError(this.handleError)
    )
    .subscribe((body) => {
      this.submitted = true;
      this.formspree = '<h2>yyy</h2>';
      console.log('submitted')
    })
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else if (error instanceof TimeoutError) {
      console.error(error.message);
    } else {


      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.');
  };

  ngOnInit() {
  }

  get diagnostic() {
    return JSON.stringify(this.model);
  }

}
